.about-us {
  z-index: 2;

background-image: url('../styles/imgs/star.jpeg');
background-position: top;
background-size: cover;
background-color: #414141;
padding-left: 3vw;
padding-bottom: 40px;
background-blend-mode: multiply;
height: 100%;
color:#ffffff;


@include for-narrow-layout {
  padding-left: 10vw;

 }
 @include for-mobile-layout {
  padding-left: 14vw;

 }

  .headline {
    font-size: 60px;
    text-align: center;
    z-index: 2;
    @include for-mobile-layout {
      font-size: 20px;
  }
  @include for-narrow-layout {
      font-size: 40px;
  }
    .fade-in-element {
      animation: slide-in 1s linear;
    }
  }
  .main-text{
    z-index: 2;

    padding-top: 20px;
    text-align: center;
    max-width: 1000px;
  }
}


@keyframes slide-in {
  0%{
    transform: translateY(0);
    filter: blur(12px);

  }
  100%{
    transform: translateY(100);
    filter: blur(0px);

  }
}