.main-header {
    height: 100vh;
    background-color: #000000;

    .crousel {
        height: 100vh;
    }

    .main-header {
        height: 100vh;
        background-color: #000000;

        .line {
            width: 100px;
            height: 4px;
            background-color: #e7212b;
            animation: blink 1.8s linear;
        }
        .main-img {
            height: 100vh;
            width: 100%;
            object-position: center;
            object-fit: cover;
            position: relative;
            filter: brightness(0.3);
        }

        .headline {
            padding-left: 14vh;
            position: absolute;
            color: #ffffff;
            font-size: 100px;
            font-weight: bolder;
            text-transform: uppercase;
            @include for-narrow-layout {
                font-size: 60px;
            }
            @include for-mobile-layout {
                font-size: 28px;
            }
        }

        .main-text {
            padding-left: 14vh;

            position: absolute;
            top: 50%;
            color: #ffffff;
            line-height: 2;
            width: 100vw;

            @include for-narrow-layout {
                font-size: 28px;
                padding-right: 4vh;
            }
            @include for-mobile-layout {
                font-size: 14px;
                padding-right: 4vh;
            }
        }
    }

    .slick-dots li.slick-active button:before {
        color: #e7212b;
        font-size: 16px;
    }

    .slick-dots li button:before {
        color: #ffffff;
        font-size: 16px;
    }

    .slick-dots {
        position: absolute;
        bottom: 20px;
    }
}

@keyframes blink {
    0% {
        width: 0px;
    }
    10% {
        width: 10px;
    }
    20% {
        width: 20px;
    }
    30% {
        width: 30px;
    }
    40% {
        width: 40px;
    }
    50% {
        width: 50px;
    }
    70% {
        width: 70px;
    }
    80% {
        width: 80px;
    }
    90% {
        width: 90px;
    }
    100% {
        width: 100px;
    }
}
