.block {
  display: block;
}
.none {
  display: none;
}

.modal {
  background: rgba(226, 106, 106, 0.6);
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  .main-content {
    // height: 100%;
    position: fixed;
    background: black;
    top: 50%;
    left: 50%;

    @include for-narrow-layout {
      top: 0;
      left: 0;
      // flex-direction: column;
    }
    @include for-mobile-layout {
      top: 0;
      left: 0;
      flex-direction: column;
    }
    button {
      color: #ffffff;
      background-color: #9b2718;
      font-size: 16px;
      padding: 5px;
      border: 0px;
    }
    .modal-text {
      height: 100%;
      padding-left: 20px;

      @include for-mobile-layout {
        width:100vw;

      }
      color: #ffffff;
      .headline {
        font-weight: bold;
        font-size: 18px;
      }
    }
  }
}

