.movie-List {
  background-color: #000000;
  padding-left: 3vw;
  z-index: 6;

  .sort {
    z-index: inherit;
    padding-top: 20px;
    :not(:last-child) {
      padding-right: 40px;
      @include for-mobile-layout {
        padding: 0px;
      }
    }

    @include for-mobile-layout {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0px;
    }
    button {
      color: #ffffff;
      background-color: transparent;
      border: 0px;
      height: 50px;
      font-size: 18px;
      cursor: pointer;
      text-align: left;
      outline:none;


      .line-btm {
        height: 1px;
        background-color: #e7212b;
        display: none;
      }
      &:hover {
        color: #e7212b;
        .line-btm {
          max-width: 40px;
          display: block;
          animation: blink 1s linear;
        }
      }
    }
  }

  .main-contant {
    .arrow {
      cursor: pointer;
      width: 2%;
      height: 2%;
      margin: 30px;
    }
  }
}

@keyframes blink {
  0% {
    width: 0px;
  }
  10% {
    width: 10px;
  }
  20% {
    width: 20px;
  }
  30% {
    width: 30px;
  }
  40% {
    width: 40px;
  }
  50% {
    width: 50px;
  }
  70% {
    width: 70px;
  }
  80% {
    width: 80px;
  }
  90% {
    width: 90px;
  }
  100% {
    width: 100px;
  }
}
