.card {
  padding: 20px;
  
  .card-text {
    font-size: 16px;
    max-width: 16ch;
    text-align: center;
    font-weight: bold;
    color:#e7212b;
  }
  .card-img {
    margin: 0;
    max-width: 250px;
    object-fit: fill;
    filter: grayscale(100%);
    transition: transform 0.3s;
    border-radius: 20px;

    &:hover {
      filter: none;
      cursor: pointer;
        transform: scale(1.1);
      
    }
  }
}

@keyframes roll {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
