.main-nav {
  height: 100%;
  width: 4vw;
  position: fixed;
  z-index: 3;
  background-color: #000000;
  color: white;


  @include for-narrow-layout {
   width: 10vw;
  }
  @include for-mobile-layout {
    width: 14vw;
  }
  


  .logo {
    font-weight: bolder;
    height: 5vh;
    background-color: #9b2718;
    margin-bottom: 30px;
  }

  .nav-text {
    color: #cacaca;
    font-weight: lighter;
    :not(:last-child) {
      margin-bottom: 44px;
    }
    .nav-wrap-icon {
      text-align: center;
    }

    .nav-icon {
      width: 50%;
      cursor: pointer;
    }

    .line {
      width: 60px;
      height: 1px;
      background-color: #e7212b;
      display: none;
    }

    li {
      font-size: 14px;
      &:hover {
        cursor: pointer;
      }
    }
  }
}
